  .head {
    text-align: center;
    padding: 40px 0px;
    color: rgb(3, 3, 66);
  

  }

  .head > img {
      height: 3cm;
      width: 5cm;
  }

  .hmain {
    text-align: center;
    display: flex;
    flex-direction: column;
    margin: 0px auto 24px;
    width: 400px;
    padding: 32px 40px;
    background: rgb(255, 255, 255);
    border-radius: 3px;
    box-shadow: rgb(0 0 0 / 10%) 0px 0px 10px;
    box-sizing: border-box;
    color: rgb(94, 108, 132);
    
}

.hmain button {
    border-radius: 3px;
    cursor: default;
    padding: 0px 8px;
    margin: 5px;
    vertical-align: middle;
    width: 100%;
    height: fit-content;
    font-size: 14px;
    font-weight: bold;
    outline: none ;
    color: rgb(66, 82, 110) ;
    height: 40px ;
    line-height: 40px ;
    background: rgb(255, 255, 255) ;
    box-shadow: rgb(0 0 0 / 20%) 1px 1px 5px 0px ;
    border: none;
}

.hmain button:hover {
    color: rgb(141, 144, 150) ;  
}

.hmain input {
   
    margin: 5px;
    width: 100%;
    background-color: rgb(250, 251, 252);
    border-color: rgb(223, 225, 230);
    border-radius: 3px;
    border-style: solid;
    
    box-sizing: border-box;
    color: rgb(9, 30, 66);
  
    font-size: 14px;
    margin-left: 4px;
    border-width: 2px;
    padding: 6px;
}

.hmain input:hover {
    background: rgb(232, 234, 236) ;
}

.hmain input:focus {
    background: white ;
    outline: none;
    border-color: skyblue;
}